<script>
  import { fade } from "svelte/transition";
  import Confetti from "./Confetti.svelte";
  import { createEventDispatcher } from "svelte";
    const dispatch = createEventDispatcher();

  export let showConfetti = true;
  export let outClickClose = true;
  export let funcClose = false;

  let isOpen = true;

  $: funcClose && (isOpen = false);

  function close() {
    if(outClickClose) {
      isOpen = false
    }

    dispatch('messageClose', false);
  }
</script>

{#if isOpen}
  <div class="completed" transition:fade="{{ y: 20 }}">
    <div class="content">
      <div class="message">
        <slot />
      </div>

      {#if outClickClose}
        <button on:click="{close}">View puzzle</button>
      {/if}
    </div>

    {#if showConfetti}
      <div class="confetti">
        <Confetti />
      </div>
    {/if}
  </div>
  <!-- svelte-ignore a11y-click-events-have-key-events -->
  <div
    class="curtain"
    transition:fade="{{ duration: 250 }}"
    on:click="{close}"></div>
{/if}

<style>
  .completed {
    position: absolute;
    top: min(50%, 20em);
    left: 50%;
    max-width: 80%;
    width: 680px;
    transform: translate(-50%, -50%);
    border-radius: 18px;
    z-index: 1000;
    box-shadow: 0 3px 6px 3px rgba(0, 0, 0, 0.2);
    background: linear-gradient(180deg, rgba(255, 201, 92, 0.70) 0%, rgba(255, 255, 255, 0.00) 155.03%);
    backdrop-filter: blur(5.838780879974365px)
  }

  .curtain {
    position: absolute;
    top: 0;
    right: -100px;
    bottom: -50px;
    left: -100px;
    background-color: var(--bg-color);
    opacity: 0.9;
    cursor: pointer;
    z-index: 999;
  }

  button {
    cursor: pointer;
    margin-left: 1em;
    font-size: 1em;
    font-family: var(--font);
    background-color: var(--accent-color);
    border-radius: 4px;
    color: var(--main-color);
    padding: 0.75em;
    border: none;
    font-weight: 400;
    transition: background-color 150ms;
  }

  button:hover {
    background-color: var(--secondary-highlight-color);
  }

  .content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 12px;
  }

  .message {
    margin-bottom: 1em;
  }

  .confetti {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
</style>
